<template>
  <div class="moneySelect" :style="item">
      <Goback title="选择银行卡"></Goback>
      <div class="moneySelect_main">
          <div class="main_box">
              <div class="box_item" 
              v-for="(item,index) in selectView"
              :key="index"
              >
                  <img :src="item.isChecked?image.checked:image.checkedNULL" alt="" />
                  <div class="item_right">
                      <div class="right_one">
                          <div>{{item.bankname}}</div>
                          <div class="one_text">尾号{{item.lastNumber}}储蓄卡</div>
                      </div>
                      <div class="item_two">
                          <img :src="image.modify" alt="" />
                          <img :src="image.del" alt="" />
                      </div>
                  </div>
              </div>
          </div>
      </div>
      <div class="fixed" @click="_changePageAdd"> <span>+</span> <div>添加银行卡</div></div>
  </div>
</template>

<script>
export default {
    name:'moneySelect',
    data(){
        return{
             item:{height:'',width:''},
             image:{
                 checked:require('@/assets/images/checked.png'),
                 checkedNULL:require('@/assets/images/checkedNULL.png'),
                 modify:require('@/assets/images/modify.png'),
                 del:require('@/assets/images/del.png'),
             },
             selectView:[
                 {id:1,bankname:'中国工商银行',lastNumber:'2343',isChecked:true},
                 {id:2,bankname:'中国农业银行',lastNumber:'2321',isChecked:false},
             ]
        }
    },
    created(){
        this.item = {
            height: document.documentElement.clientHeight + "px",
            width: document.documentElement.clientWidth + "px"
        };
    },
    methods:{
        _changePageAdd(){
            this.$router.push({ path:'/moneyAdd'})
        }
    }
}
</script>

<style lang="scss">
@import './index.scss';
</style>


